/* eslint-disable react-hooks/exhaustive-deps */
import { useWeb3React } from "@web3-react/core";
import { useCallback, useEffect, useMemo, useState } from "react";
import chunk from "lodash/chunk";
import { useChainId, useWeb3 } from "../packages/web3/hooks";
import IPoolInfo from "../types/IPoolInfo";
import {
  usePoolFactoryContract,
  usePoolFactoryManagerContract,
  useTokenManagerContract,
} from "./useContract";
import usePoolState from "./usePoolState";
import IBlacklistPool from "../types/IBlacklistPool";
import { getBlacklistPools, poolWithInfo } from "../utils/pool";

let timer: any = null;
let interval: any = null;
let firstLoad = true;

interface IPool extends IPoolInfo {
  tokenInfo?: {
    name: string;
    decimals: string;
    symbol: string;
    totalSupply: string;
    isTokenGenerated: boolean;
  };
}

export default function usePools(
  page: number,
  pageSize = 18,
) {
  const web3 = useWeb3();
  const contract = usePoolFactoryManagerContract(web3);
  const factoryContract = usePoolFactoryContract(web3);
  const tokenManagerContract = useTokenManagerContract(web3);
  const chainId = useChainId();
  const { account } = useWeb3React();
  const [poolAddresses, setPoolAddresses] = useState<string[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [loaded, setLoaded] = useState<boolean>(false);
  const [tick, setTick] = useState<number>(1);
  const [, setPools] = usePoolState();

  const poolWithPage = useMemo<string[]>(() => {
    const pools = chunk(poolAddresses, pageSize);
    return pools[page - 1] || [];
  }, [poolAddresses, page, pageSize]);

  const getPools = useCallback(
    (addresses: string[]) => {
      (async () => {
        if (addresses.length === 0) return;
        if (firstLoad) {
          setLoading(true);
          setLoaded(false);
        }
        try {
          const data: { [address: string]: Partial<IPool> } = {};
          const promises = [];
          for (let i = 0, len = addresses.length; i < len; i++) {
            promises.push(
              poolWithInfo(
                addresses[i],
                web3,
                tokenManagerContract,
                factoryContract,
                account,
                chainId
              )
            );
          }
          const fetchedPools = await Promise.all(promises);
          fetchedPools.map((info) => {
            data[info.address] = {
              ...info.pool,
              ...info.additionalInfo,
              tokenInfo: info.token,
              address: info.address,
            };
            return null;
          });
          setPools(data);
        } catch (e) {
          // console.log(e);
        } finally {
          if (firstLoad) {
            setLoading(false);
            setLoaded(true);
          }
          firstLoad = false;
        }
      })();
    },
    [contract, chainId, web3, tokenManagerContract, account]
  );

  useEffect(() => {
    if (!contract) return;
    clearTimeout(timer);
    timer = setTimeout(() => {
      (async () => {
        try {
          const blacklist = await getBlacklistPools();
          const blacklistByChain = blacklist
            .filter((p: IBlacklistPool) => p.chainId === chainId)
            .map((p: IBlacklistPool) => p.address);
          const result = await contract.methods.getAllPools().call();
          const enabledPools =
            blacklistByChain.length > 0
              ? result.filter((v: string) => blacklistByChain.indexOf(v) < 0)
              : result;
          const pools = [...enabledPools];
          pools.reverse();
          setPoolAddresses(pools);
        } catch (e) {
          //
        }
      })();
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [contract, chainId, tick]);

  useEffect(() => {
    if (firstLoad) return;
    firstLoad = true;
  }, [page, chainId]);

  useEffect(() => {
    getPools(poolWithPage);
  }, [poolWithPage]);

  useEffect(() => {
    if (!loaded) return;
    clearTimeout(interval);
    interval = setTimeout(() => {
      setTick((prev) => prev + 1);
    }, 5000);
    return () => {
      clearTimeout(interval);
    };
  }, [loaded]);

  return {
    pools: poolAddresses,
    loading,
  };
}
