import { createGlobalState } from "react-use";
import axios from "axios";
import { useEffect } from "react";
import { useWeb3React } from "@web3-react/core";

type TState = {
  blacklist: Array<{
    address: string;
    chainId: number;
  }>;
};

const shared = createGlobalState<TState>({
  blacklist: [],
});

export function useFetchLeaderboardBlacklist() {
  const [, setBlacklist] = shared();
  useEffect(() => {
    axios.get('/leaderboard_blacklist.json').then(({ data }) => {
      if (Array.isArray(data)) {
        setBlacklist({
          blacklist: data,
        });
      }
    }).catch((e) => e);
  }, [setBlacklist]);
}

export default function useLeaderboardBlacklist() {
  const { chainId } = useWeb3React();
  const [state] = shared();
  if (!chainId) return null;
  if (state.blacklist.length === 0) return null;
  return state.blacklist.filter((p) => p.chainId === chainId).map((p) => p.address);
}
