import { useCallback, useEffect, useState } from "react";
import { useWeb3React } from '@web3-react/core';
import { useWeb3 } from "../packages/web3/hooks";
import { useTokenManagerContract } from "./useContract";
import multicall from "../utils/multicall";

let timer: any = null;
const ERC20Abi = require('../abi/ERC20Abi.json');

type TToken = {
  name: string;
  symbol: string;
  decimals: number;
  address: string;
  totalSupply: string;
  type: number;
};

export default function useMyTokens() {
  const web3 = useWeb3();
  const contract = useTokenManagerContract(web3);
  const [loading, setLoading] = useState<boolean>(false);
  const [tokens, setTokens] = useState<TToken[]>([]);
  const { account, chainId } = useWeb3React();

  const getTokens = useCallback((useInterval = false) => {
    if (!contract || !account || !chainId) return;
    (async () => {
      if (!useInterval) {
        setLoading(true);
      }
      try {
        const result = await contract.methods.getAllTokens(account).call();
        const tokenList = result[0], typeList = result[1];
        if (Array.isArray(tokenList)) {
          const tokenArr = [];
          for (let i = 0, len = tokenList.length; i < len; i++) {
            const calls = ['name', 'symbol', 'decimals', 'totalSupply'].map((method) => ({
              address: tokenList[i],
              name: method,
            }));
            const [[name], [symbol], [decimals], [totalSupply]] = await multicall(ERC20Abi, calls, {
              web3,
            }, chainId);
            tokenArr.push({
              name,
              symbol,
              decimals: decimals.toString(),
              address: tokenList[i],
              totalSupply: totalSupply.toString(),
              type: Number(typeList[i])
            });
          }
          setTokens(tokenArr);
        }
      } catch (e) {
        //
      } finally {
        if (!useInterval) {
          setLoading(false);
        }
      }
    })();
  }, [contract, account, web3, chainId]);

  useEffect(() => {
    clearInterval(timer);
    getTokens();
    timer = setInterval(() => {
      getTokens(true);
    }, 3000);

    return () => {
      clearInterval(timer);
    };
  }, [getTokens]);

  return {
    loading,
    tokens,
  };
}
