import axios from "axios";
import { useAsync } from "react-use";
import configs from "../configs";
import { useChainId } from "../packages/web3/hooks";

export default function useLeaderboard(miliseconds?: number) {
  const chanId = useChainId();
  const result = useAsync(async () => {
    return (await axios.get(`${configs.API_BASE_URL}/leaderboard/list?date=${miliseconds || ''}&chainId=${chanId}`)).data;
  }, [miliseconds]);
  return result;
}
